button {
  line-height: 50px;
  height: 50px;
  text-align: center;
  width: 250px;
  cursor: pointer;
}

button {
  font-size: 20px;
  color: rgb(10, 4, 4);
  transition: all 0.3s;
  position: relative;
}
button span {
  transition: all 0.3s;
}
button::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border-top-width: 1px;
  border-bottom-width: 1px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(46, 39, 39, 0.5);
  border-bottom-color: rgba(36, 23, 23, 0.5);
  -webkit-transform: scale(0.1, 1);
          transform: scale(0.1, 1);
}
button:hover span {
  letter-spacing: 8px;
}
button:hover::before {
  opacity: 1; 
  -webkit-transform: scale(1, 1); 
          transform: scale(1, 1); 
}
button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s;
  background-color: rgba(12, 3, 3, 0.1);
}
button:hover::after {
  opacity: 0; 
  -webkit-transform: scale(0.1, 1); 
          transform: scale(0.1, 1);
}
